import kotlinx.css.Color
import kotlinx.css.Display
import kotlinx.css.backgroundColor
import kotlinx.css.display
import kotlinx.html.js.onClickFunction
import react.*
import styled.css
import styled.styledButton

external interface VideoButtonProps : RProps {
    var function: () -> Unit
    var unwatchedVideo: Boolean
}

class VideoButton : RComponent<VideoButtonProps, RState>() {
    override fun RBuilder.render() {
        styledButton {
            css {
                display = Display.block
                backgroundColor = if (props.unwatchedVideo) Color.lightGreen else Color.red
            }
            attrs {
                onClickFunction = {
                    props.function()
                }
            }
            if (props.unwatchedVideo) {
                +"Mark as watched"
            } else {
                +"Mark as unwatched"
            }
        }
    }
}

fun RBuilder.videoButton(handler: VideoButtonProps.() -> Unit): ReactElement {
    return child(VideoButton::class) {
        this.attrs(handler)
    }
}
